import Button from "./elements/Button";
import React from "react";
import { Link } from "react-router-dom";
export const Banner = () => {
    const navigateToMenu = () => {
        window.location.href = "/menu";
    };
    return (
        <div className="banner w-full md:w-2/3 px-7 mx-auto relative flex items-center-justify-between">
            <div className="banner-description w-full md:w-1/2 p-3">
                <h2 className="mb-6 text-4xl font-bold text-white">
                    Giancarlo's Online
                </h2>
                <p className="font-semibold text-lg text-green-600 py-2">
                    Order Today!
                </p>
                <div className="btn-container">
                    <Button onClick={navigateToMenu}>Order Now</Button>
                    <Link to="/menu" className="text-green-600 hover:text-red-500 font-bold text-decoration-line px-3">Menu</Link>

                </div>
            </div>
            <div className="banner-image w-full md:w-1/2 p-3 flec justify-end">
                <img src={require("../assets/images/cake-banner.png")} alt="banner" className="max-h-95" />
            </div>
        </div>
    )
}